import { createApp } from 'vue';
import router from './';
import app from './app.vue';

// Vue Router$(() => {})
const vueApp = createApp(app);
vueApp.use(router);

// 
const perfStartTime = performance.now();

// Web Vitals
const initWebVitalsMonitoring = () => {
  try {
    // LCP (Largest Contentful Paint)
    const lcpObserver = new PerformanceObserver(entryList => {
      const entries = entryList.getEntries();
      const lastEntry = entries[entries.length - 1];
      console.log(`[Web Vitals] LCP: ${lastEntry.startTime.toFixed(2)}ms`, lastEntry);
    });
    lcpObserver.observe({ type: 'largest-contentful-paint', buffered: true });

    // FID (First Input Delay) - Event Timing API
    const fidObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (entry.entryType === 'first-input') {
          console.log(`[Web Vitals] FID: ${entry.processingStart - entry.startTime}ms`, entry);
        }
      }
    });
    fidObserver.observe({ type: 'first-input', buffered: true });

    // CLS (Cumulative Layout Shift)
    let clsValue = 0;
    let clsEntries: any[] = [];
    const clsObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (!entry.hadRecentInput) {
          clsEntries.push(entry);
          clsValue += entry.value;
          console.log(`[Web Vitals] CLS: ${clsValue.toFixed(4)}`, entry);
        }
      }
    });
    clsObserver.observe({ type: 'layout-shift', buffered: true });

    //  (Long Tasks)
    const longTaskObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (entry.duration > 50) {
          // 50ms
          console.log(`[] : ${entry.duration.toFixed(2)}ms`, entry);
        }
      }
    });
    longTaskObserver.observe({ type: 'longtask', buffered: true });

    console.log('[] Web Vitals');
  } catch (error) {
    console.warn('[] Web Vitals:', error);
  }
};

// Chrome
const initMemoryMonitoring = () => {
  try {
    // performance.memory API
    if ('memory' in performance) {
      const memory = (performance as any).memory;
      console.log(
        `[] : ${(memory.usedJSHeapSize / 1024 / 1024).toFixed(2)}MB / ${(memory.totalJSHeapSize / 1024 / 1024).toFixed(2)}MB`,
      );

      // 
      setInterval(() => {
        const mem = (performance as any).memory;
        const usedMB = mem.usedJSHeapSize / 1024 / 1024;
        const totalMB = mem.totalJSHeapSize / 1024 / 1024;
        const percentage = ((usedMB / totalMB) * 100).toFixed(1);

        // 
        if (usedMB > 50 || percentage > 80) {
          console.log(`[] : ${usedMB.toFixed(2)}MB/${totalMB.toFixed(2)}MB (${percentage}%)`);
        }
      }, 30000); // 30

      console.log('[] ');
    } else {
      console.log('[] performance.memory API');
    }
  } catch (error) {
    console.warn('[] :', error);
  }
};

// 
const preloadCriticalRoutes = async () => {
  try {
    // 
    const criticalRoutes = ['/service', '/history'];
    await Promise.all(
      criticalRoutes.map(path => {
        const route = router.resolve(path);
        // Vue Router
        return route;
      }),
    );
    console.log('[] ');
  } catch (error) {
    console.warn('[] :', error);
  }
};

// 
const preloadCriticalResources = async () => {
  try {
    // 
    const resources: string[] = [
      // 
    ];

    await Promise.all(
      resources.map(url => {
        return new Promise((resolve, reject) => {
          const link = document.createElement('link');
          link.rel = 'prefetch';
          link.href = url;
          link.onload = resolve;
          link.onerror = reject;
          document.head.appendChild(link);
        });
      }),
    );
    console.log('[] ');
  } catch (error) {
    console.warn('[] :', error);
  }
};

// MVU
const waitForMvu = async (timeout = 5000) => {
  return Promise.race([
    waitGlobalInitialized('Mvu'),
    new Promise((_, reject) => setTimeout(() => reject(new Error('MVU')), timeout)),
  ]).catch(err => {
    console.warn('[MVU] :', err);
    return null; // null
  });
};

// 
const initApp = async () => {
  try {
    // 
    const initTasks = Promise.all([
      waitForMvu(), // MVU
      preloadCriticalRoutes(), // 
      preloadCriticalResources(), // 
    ]);

    // 
    const [mvuReady] = await initTasks;

    // Web Vitals
    initWebVitalsMonitoring();
    // 
    initMemoryMonitoring();

    // 
    const initTime = performance.now() - perfStartTime;
    console.log(`[] : ${initTime.toFixed(2)}ms`);

    // MVU
    if (mvuReady) {
      console.log(`[] (${initTime.toFixed(0)}ms)`);
    } else {
      console.log('[] ');
    }

    // Vue
    vueApp.mount('#app');

    // 
    const mountTime = performance.now() - perfStartTime;
    console.log(`[] : ${mountTime.toFixed(2)}ms`);
  } catch (error) {
    console.error('[] :', error);
    console.log('[] ');
  }
};

//  jQuery  DOMContentLoaded
$(() => {
  //  - VueonMounted
  initApp();
});

//  jQuery 
$(window).on('pagehide', () => {
  const unloadTime = performance.now() - perfStartTime;
  console.log(`[] : ${unloadTime.toFixed(2)}ms`);
  console.log('[] ');
});

//  - Promise
window.addEventListener('unhandledrejection', event => {
  console.error('[] Promise:', event.reason);
  console.log('[] ');
});

//  - JavaScript
window.addEventListener('error', event => {
  console.error('[] JavaScript:', event.error);
});
